<template lang="pug">
v-container#wrapper.pa-0
  v-row(no-gutters)
    //- tag
    v-col.tag.d-flex.justify-center.align-center(cols="auto") 
      svg#btntag(@click="showPlaceInfo")
        use(xlink:href="#tag")
    //- photo
    v-col.photo.fill-height.d-flex.justify-center.align-center.pa-2(
      cols="auto"
    )
      img(
        :src="place.photo_url ? place.photo_url : 'https://doqvf81n9htmm.cloudfront.net/data/Luke1226_165/2020-02/%E5%92%96%E5%95%A1%E5%BB%B3/%E5%8F%B0%E5%8C%97%E7%99%AE%E5%92%96%E5%95%A1_40a.jpg'"
      )
    //- name
    v-col.name.d-flex.justify-center.px-8(align-self="center") {{ place.name }}
    //- phone
    v-col.phone.d-flex.justify-center(cols="auto", align-self="center") {{ place.phone }}
    //- pin
    v-col.pin.d-flex.justify-center(cols="auto", align-self="center") 
      a(
        :href="'https://www.google.com/maps/search/?api=1&query=' + place.name + '&query_place_id=' + place.gmap_id",
        target="_blank"
      )
        svg#btnpin
          use(xlink:href="#pin")
    //- select
    v-col.select.d-flex.justify-center(
      cols="auto",
      align-self="center",
      @click.stop="toggleSelect()"
    )
      //- (:style="{ fill: selected ? '#e7c99e' : '#EDE8DF' }")
      svg#btnselect(:class="{ selected: selected}")
        use(xlink:href="#select")
</template>

<script>
export default {
  props: {
    place: { type: Object },
    allSelected: { type: Boolean },
  },
  
  data() {
    return {
      selected: false,
      setActive: false,
    };
  },
  methods: {
    toggleSelect: function () {
      this.selected = !this.selected;
      if (this.selected == true) this.$emit("placeSelected", this.place.id);
      else this.$emit("placeUnSelected", this.place.id);
    },
    showPlaceInfo: function () {
      this.setActive = true;
      this.$emit("showPlaceInfo", {
        placeId: this.place.id,
        setActive: this.setActive,
      });
    },
  },
};
</script>



<style lang="sass" scoped>
$width-base: 50px
$margin-base: 12px

*
  // outline: 1px red solid
  font-size: 12px

svg
  width: 18px
  height: 18px

  cursor: pointer

.tag, .pin, .select, .first-space
  width: $width-base

.name
  overflow: auto
  white-space: nowrap
  letter-spacing: 1px

.photo
  width: $width-base*2
  height: $width-base*1.6
  img
    border-radius: 3px
    width: 100%
    height: 100%
    object-fit: cover

.phone
  width: $width-base*2

#wrapper
  background-color: transparent
  &:hover
    background-color: #f8f8f8
    cursor: default
  &.rowChosen
    background-color: #f8f8f8
#btntag
  fill: #d1d5d9
  &:hover
    fill: #f6e18e
#btnpin, #btnselect
  fill: #d1d5d9
  &:hover
    fill: #6a80a3
#btnselect
  fill: #d1d5d9
  &:hover
    fill: #e7c99e
  &.selected
    fill: #e7c99e
</style>