<template lang="pug">
v-card
  v-card-title.justify-center 上傳照片
    v-row.photo
      img(:src="url + photoUrl")
    input#reupload(
      type="file",
      accept="image/*",
      @change="uploadImage($event)"
    ) 
    v-row
      v-col.d-flex.justify-start 
        v-btn.btn.cancel(@click="cancel") 取消
      v-col.d-flex.justify-end
        v-btn.btn.redo.mx-2(@click="redo") 重傳
        v-btn.btn.save(@click="save") 儲存
</template>

<script>
import { url } from "@/services/Const";
export default {
  props: {
    photoUrl: { type: String },
    listId: {type: Number},
  },
  data() {
    return {
      url: url.baseUrl,
    };
  },
  methods: {
    cancel: function () {
      this.$emit("closeUpload");
    },
    redo: function () {
      document.getElementById("reupload").click();
    },
    save: function(){
      let coverUrl = url.baseUrl + this.photoUrl;
      this.$store.dispatch("user/setListCover", {list_id: this.listId, cover_image_url: coverUrl })
      console.log(coverUrl, url.baseUrl, this.photoUrl)
      this.$emit("setListCover");
    },
    uploadImage(event) {
      console.log(event.target.files[0])
      let data = new FormData();
      data.append("file", event.target.files[0]);
      this.$store.dispatch("user/saveListPhoto", data);
    },
  },
};
</script>

<style lang="sass" scoped>
$width: 100%
.photo
  width: 100%
  height: 100%
img
  width: 100%
  height: 100%
  object-fit: cover

input[type="file"]
  display: none
</style>